import React from 'react';

const RecipeSearch = ({ setSearchField, setAutoCompleteList, addTag }) => {
  const handleChange = async (e) => {
    setSearchField(e.target.value);
    const result = await fetch(
      `https://api.edamam.com/auto-complete?q=${e.target.value}&limit=10&app_id=683be2ba&app_key=96cd3002521f4bed4a8593c0856ed65f`,
    );
    const data = await result.json();
    setAutoCompleteList(data);
  };

  return (
    <div className="tc ma3 v-mid">
      <input
        className="input-reset b--black-20 pa2 ma1 w-70 br3"
        placeholder="Add ingredients..."
        onChange={handleChange}
      />
      <button
        onClick={addTag}
        className="b--black-20 pa2 ma1 br3 bg-dark-blue white"
      >
        add
      </button>
    </div>
  );
};

export default RecipeSearch;
